import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, NavLink } from 'react-router-dom'
import { checkingFinish } from '../../actions/usuario'

export const Navbar = () => {

    const dispatch = useDispatch()
    const Logout = () => {
        dispatch(checkingFinish())
    }

    const {checking} = useSelector(state => state.usuario)

    if(checking) return (

        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
            <div className="container-fluid">
                <Link 
                    className="navbar-brand" 
                    to="/"
                >
                    Home
                </Link>
                
                <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
                <div className="navbar-nav">
                    <NavLink 
                        className={({isActive}) => 'nav-link ' + (isActive ? 'active' : '')}
                                                
                        to="app/cliente"
                    >
                        Cliente
                    </NavLink>

                    <NavLink 
                        className={({isActive}) => 'nav-link ' + (isActive ? 'active' : '')}
                        
                        to="app/actividades"
                    >
                        Actividades
                    </NavLink>
                    <NavLink 
                        className={({isActive}) => 'nav-link ' + (isActive ? 'active' : '')}
                        
                        to="app/presupuesto"
                    >
                        Presupuesto
                    </NavLink>
                    <NavLink 
                        className={({isActive}) => 'nav-link ' + (isActive ? 'active' : '')}
                        
                        to="app/reporte"
                    >
                        Reportes
                    </NavLink>
                    
                </div>
                </div>
                <div>
                    <NavLink 
                        className={({isActive}) => 'nav-link ' + (isActive ? 'active' : '')}
                        
                        to="usuario/login"
                    >
                        Login
                    </NavLink>

                    <NavLink 
                        className={({isActive}) => 'nav-link ' + (isActive ? 'active' : '')}
                        onClick={Logout}
                        
                        to="usuario/login"
                    >
                        Logout
                    </NavLink>

                </div>
            </div>
        </nav>
       
    )

    return (

        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <div className="container-fluid">
            <Link 
                className="navbar-brand" 
                to="/"
            >
                Home
            </Link>
            
            <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
            <div className="navbar-nav">
                <NavLink 
                    className={({isActive}) => 'nav-link ' + (isActive ? 'active' : '')}
                                            
                    to="/proyectos"
                >
                    Proyectos
                </NavLink>

                <NavLink 
                    className={({isActive}) => 'nav-link ' + (isActive ? 'active' : '')}
                    
                    to="/contactos"
                >
                    Constactos
                </NavLink>
                
                
            </div>
            </div>
            <div>
                <NavLink 
                    className={({isActive}) => 'nav-link ' + (isActive ? 'active' : '')}
                    
                    to="usuario/login"
                >
                    Login
                </NavLink>

                <NavLink 
                    className={({isActive}) => 'nav-link ' + (isActive ? 'active' : '')}
                    onClick={Logout}
                    
                    to="usuario/login"
                >
                    Logout
                </NavLink>

            </div>
        </div>
    </nav>

    )
}