import React from 'react';
import jsPDF from 'jspdf'
import 'jspdf-autotable'
import { numerosaLetras } from '../../helpers/numerosLetras';


export const PdfScreen = ({cliente, fecha, total, listActividades}) => {

  const {cedula, nombre, apellido, ciudad, direccion} = cliente

  const totalLetras = numerosaLetras(total);

  const nombreColumnas = [['Item','Descripcion', 'Unidad', 'Cantidad', 'PrecioUnitario', 'Total']]
  const datos = [];
  let cont = 0;
  listActividades.map(act=> {
    let actividad =[]
    cont += 1
    actividad.push( cont )
    actividad.push(act.descripcion)
    actividad.push(act.unidad)
    actividad.push(act.cantidad.toFixed(2))
    actividad.push(`$${new Intl.NumberFormat('ja-JP').format(act.precio_unitario.toFixed(2))}`)
    actividad.push(`$${new Intl.NumberFormat('ja-JP').format(act.total.toFixed(2))}`)
    datos.push(actividad)
  })

  const datosCliente = [
    `Nombre: ${nombre} ${apellido}`,
    `Ciudad: ${ciudad}`,
    `Direccion: ${direccion}`,
    `Fecha: ${fecha}`

  ]


  const piePresupuesto = [
    '_______________________________________________________',
    'FERNEY ANDRES DIAZ ROMERO',
    'Tec. Lab. En  Construcción de obras civiles, avalúos  y topografía.'
  ]


  const pieContratoContratante = [
    'EL CONTRATANTE',
    ' ',
    ' ',
    '_____________________ ',
    `${nombre} ${apellido}`,
    `CC ${cedula}`
  ]

  const pieContratoContratista =[
    'EL CONTRATISTA  ',
    ' ',
    ' ',
    '_____________________',
    `Constructor de Obras Civiles`,
    `Ferney Andrés Díaz Romero`,
    'CC: 1.131.109.292'
  ]


  const generatePDF = () => {
    var doc = new jsPDF('p', 'mm','letter')

    
    const logoData = 'data:image/png;base64,';

    const render = 'data:image/jpeg;base64,';

    //////Presupuesto////////////////////////////////////

    // Encabezado ////////

    doc.addImage(logoData, 'JPG', 20,6,40,40)

    doc.roundedRect(100,10,100,32,3,3)
    doc.setFontSize(12)
    doc.setFont('times', 'bold')
    doc.text('CONSTRUCCIÓN CIVIL REMODELACIÓN Y AMPLIACIÓN DE VIVIENDAS', 105,18, {maxWidth:95} )
    
    doc.setFontSize(10)
    doc.setFont('times', 'normal')
    doc.text('FERNEY ANDRES DIAZ ROMERO. Tec. Lab. En  Construcción de obras civiles, avalúos  y topografía. (Politécnico INDES) Dic. 2011', 105,30, {maxWidth:90} )
    ///////////////////////////////

    doc.setFontSize(14)
    doc.setFont('times', 'bold')
    doc.text('PRESUPUESTO MEJORAMIENTO DE VIVIENDA',50,60)

    doc.setFontSize(12)
    doc.setFont('times', 'normal')
    doc.text(datosCliente, 20,73)

    doc.addImage(render, 'JPG', 90, 65, 40,30)

    doc.autoTable({
      head: nombreColumnas,
      body: datos,
      margin: {top: 100},
      cellWidth: {with:'auto'},
      maxWidth:180
    })

    doc.setFontSize(12)
    doc.text(`Total presupuesto   ……………………   $${new Intl.NumberFormat('ja-JP').format(total.toFixed(2))}`, 20, 180, {maxWidth:180})
    doc.text('Forma de pago,                                               Efectivo ', 20,200, {maxWidth:180})
    doc.text(piePresupuesto, 20,228)
    
    //////Contrato///////

    doc.addPage()

    // Encabezado ////////

    doc.addImage(logoData, 'JPG', 20,6,40,40)
    doc.setFont('times', 'bold')
    doc.roundedRect(100,10,100,32,3,3)
    doc.setFontSize(12)
    doc.text('CONSTRUCCIÓN CIVIL REMODELACIÓN Y AMPLIACIÓN DE VIVIENDAS', 105,18, {maxWidth:95} )
    
    doc.setFontSize(10)
    doc.setFont('times', 'normal')
    doc.text('FERNEY ANDRES DIAZ ROMERO. Tec. Lab. En  Construcción de obras civiles, avalúos  y topografía. (Politécnico INDES) Dic. 2011', 105,30, {maxWidth:90} )
    ///////////////////////////////

    doc.setFontSize(14)
    doc.setFont('times', 'bold')
    doc.text('CONTRATO DE OBRA', 80, 70,)

    doc.setFontSize(12)
    doc.setFont('times', 'normal')
    doc.text(`Entre los suscritos a saber ${nombre} ${apellido}   mayor de edad y vecino de ${ciudad}, identificado con cedula de ciudadanía N° ${cedula}  quien en adelante se denominará  contratante y por otra parte FERNEY ANDRES DIAZ ROMERO  identificado con la cedula de ciudadanía N° 1.131.109.292 expedida en Coveñas, con fecha de diploma de técnico laboral en construcciones civiles, avalúos y  topografías, (diciembre de 2011), quien en adelante se denominara el contratista, se ha celebrado el contrato expresado en las siguientes cláusulas del presente documento: PRIMERA: el presente contrato tiene por objeto: MEJORAMIENTO DE SU VIVIENDA propiedad de ${nombre} ${apellido} en el municipio de ${ciudad}, direccion ${direccion} según contrato de compra y venta. SEGUNDA: Que el contratista se compromete a efectuar MEJORAMIENTO DE SU VIVIENDA, que tiene las siguientes características bajo su propia responsabilidad sin que genere vínculo laboral alguno entre el contratante y el contratista y con las personas que emplee el contratista. TERCERA: precios y valor: De acuerdo a las cantidades de obra y precios unitarios indicados a continuación el valor del presente contrato asciende a la suma de ${totalLetras} ( $ ${new Intl.NumberFormat('ja-JP').format(total.toFixed(2))}) m. cte. CLAUSULA CUARTA: En caso de incumplimiento de alguna de las partes estas pactan al mutuo acuerdo una clausula penal de 02 dos millones de pesos.`,
     20,90, {align:'justify', maxWidth:180, lineHeightFactor:2})
    doc.text(pieContratoContratante,20,240)
    doc.text(pieContratoContratista,120,240)
    
    

    doc.save(`${nombre} ${apellido}.pdf`)
  }      
  
 //render() 
    return (
       <div>
          <button 
            className='btn btn-info'
            onClick={generatePDF}
          >
            Descargar PDF
          </button> 
       </div>
    )

    
 
}







